  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  font-family: 'Poppins', sans-serif;
  font-size: 12px;
  color: #424242;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* 
Skip-link
*/

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #679b9b;
  color: #fff;
  padding: 8px;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}
 
 
/*
  AppBar
*/
 
.app-bar {
  padding: 8px 16px;
  background-color: #ffb69f;
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}
 
.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
}
 
.app-bar .app-bar__menu button {
  display: flex;
  justify-content: center;
  background-color: transparent;
  border: none;
  min-width: 44px;
  min-height: 44px;
  font-size: 19px;
  padding: 12px;
  cursor: pointer;
  color: #fff;
}
 
.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
}
 
.app-bar .app-bar__brand h1 {
  color: #ffffff;
  text-transform: uppercase;
  font-size: 22px;
  user-select: none;
}
 
.app-bar .app-bar__navigation {
  position: absolute;
  top: 50px;
  left: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 8px;
  background-color: #ffb69f;
  overflow: hidden;
}
 
.app-bar .app-bar__navigation.open {
  left: 0;
}
 
.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: #ffffff;
  padding: 8px;
  margin-bottom: 5px;
  width: 100%;
  font-size: 1.2rem;
}

/*
 * Jumbotron
 */

 .jumbotron {
  display: flex;
  align-items: center;
  min-height: 380px;
  width: 100%;
  text-align: center;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
}

 .jumbo-inner {
  margin: 0 auto;
  max-width: 800px;
}

.jumbo-title {
  color: #fff;
  font-weight: 500;
  font-size: 38px;
  text-shadow: 2px 2px 3px #111;
}

.jumbo-tagline {
  color: #fff;
  margin-top: 16px;
  font-size: 20px;
  font-weight: 300;
  text-shadow: 2px 2px 3px #123;
}

 
/*
  Main Content
*/
main {
  width: 100%;
  margin: 0 auto;
}

.content {
  margin: 0 auto;
  min-height: 100%;
}

  .main-content{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
.main-title h1{
    font-weight: bold;
    text-align: center;
    margin-top: 30px;
    margin-bottom: 20px;
    font-size: 1.8em;
    color: #424242;
}

  .body-card {
    width: 90%;
    display: grid;
    grid-template-columns: 1fr;
    gap: 16px;
    margin: 32px 0;
  }

  .body-card .resto-card {
    width: 100%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    overflow: hidden;
  }

  .resto-item__header {
    position: relative;
  }
  
  .resto-card .resto-item__header .resto-item__header__image {
    width: 100%;
    object-fit: cover;
    max-height: 200px;
    }
  
  .body-card .resto-card .name{
    background-color:#D44000;
    color: #ffffff;
    padding:10px;
    font-size:18px;
  }

  .resto-card .resto-item__content h3 {
    margin: 0 0 10px 0;
  }

  .resto-card .resto-item__content h3 a {
    color: #424242;
    font-size: 20px;
    padding: 12px 5px 12px 0;
    text-decoration: none;
  }

  .resto-card .resto-item__content p {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4; /* number of lines to show */
    -webkit-box-orient: vertical;
  }

  .resto-card .resto-item__header .resto-item__header__rating {
    position: absolute;
    padding: 8px;
    bottom: 20px;
    left: 0;
    display: inline-block;
    background-color: #ffb69f;
    color: white;
    border-top-right-radius: 14px;
  }

  .resto-card .resto-item__header .resto-item__header__rating .resto-item__header__rating__score {
    margin-left: 10px;
    margin-right: 5px;
  }
  
  .body-card .resto-card .resto-item__content {
    padding: 16px;
  }

  .city {
    position: absolute;
