    background-color: #ffb69f;
    color: #ffffff;
    padding: 10px;
    font-size: 15px;
  }

  .checked{
    color: #111;
  }

  .detail-resto-title{
    padding: 40px;
  }
  
  .detail-resto-title .detail-resto-title-section{
    margin-bottom: 22px;
  }
  .detail-resto-title .detail-resto-title-section h1{
    font-size: 1.5em;
