  }
  .detail-resto-title .detail-resto-info p{
    color: #3d3d3d;
    text-align: justify;
  }
  .detail-resto-title .detail-resto-categories{
    margin-top: 15px;
  }
  
  .detail-resto-title .detail-resto-categories ul li{
    display: inline-block;
    padding: 5px;
    margin:5px;
    background-color: #ffb69f;
    color: #ffffff;
    border-radius: 5px 10px 5px 10px;
  }
  
  .detail-resto-menu{
    text-align: center;
    width: 100%;
    margin-top: 0;
  }
  .detail-resto-menu h1{
    width:100%;
    padding: 10px;
