    border-bottom: 1px solid #f0b452;
    border-radius: 10px 5px 10px 5px;
  
  }
  
  .menu h2{
    /* text-decoration: underline; */
    padding: 10px;
    background-color: #ffb69f;
    border-radius: 10px 10px 0 0;
    color: #fff;
  }
  
  .menu ul{
    width: 100%;
    padding: 20px;
    background-color:#FDF4F5;
    border: 1px solid #ffffff;
    border-radius:  0 0 10px 10px;
  }
  
  .menu ul li{
    color:#111;
    font-size: 16px;
    margin-left: 20px;
    padding: 5px;
    list-style:circle;
    text-align: justify;
  
  }
  
  .resto-review-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 40px;
    padding: 25px;
    height: 30%;
    overflow: auto;
    color: #111;
