    margin-bottom: 0;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
      rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  }
  
  .resto-review-section h1{
    margin-top: 50px;
    margin-bottom: 50px;
    color: #111;
  }
  .resto-review-section .resto-review-user {
    width: 80%;
    display: flex;
    margin: 10px;
    padding-bottom: 20px;
    border-bottom: 1px solid #db0000;
  }
  
  .resto-review-section .resto-review-user img {
    width: 40px;
    height: 40px;
    margin: 7px;
  }
  
  .resto-review-section .resto-review-user .resto-review-desc {
    margin-top: 10px;
    margin-bottom: 5px;
    font-size: 17px;
  }
  
  .resto-review-section .resto-review-user .resto-review-date {
    font-size: 15px;
