em;\n}\n\n/*\n * Jumbotron\n */\n\n .jumbotron {\n  display: flex;\n  align-items: center;\n  min-height: 380px;\n  width: 100%;\n  text-align: center;\n  background-image: url(\"/src/public/images/heros/hero-image_1.jpg\");\n  background-position: center;\n}\n\n .jumbo-inner {\n  margin: 0 auto;\n  max-width: 800px;\n}\n\n.jumbo-title {\n  color: #fff;\n  font-weight: 500;\n  font-size: 38px;\n  text-shadow: 2px 2px 3px #111;\n}\n\n.jumbo-tagline {\n  color: #fff;\n  margin-top: 16px;\n  font-size: 20px;\n  font-weight: 300;\n  text-shadow: 2px 2px 3px #123;\n}\n\n \n/*\n  Main Content\n*/\nmain {\n  width: 100%;\n  margin: 0 auto;\n}\n\n.content {\n  margin: 0 auto;\n  min-height: 100%;\n}\n\n  .main-content{\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n  }\n  \n.main-title h1{\n    font-weight: bold;\n    text-align: center;\n    margin-top: 30px;\n    margin-bottom: 20px;\n    font-size: 1.8em;\n    color: #424242;\n}\n\n  .body-card {\n    width: 90%;\n    display: grid;\n    grid-template-columns: 1fr;\n    gap: 16px;\n    margin: 32px 0;\n  }\n\n  .body-card .resto-card {\n    width: 100%;\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n    border-radius: 8px;\n    overflow: hidden;\n  }\n\n  .resto-item__header {\n    position: relative;\n  }\n  \n  .resto-card .resto-item__header .resto-item__header__image {\n    width: 100%;\n    object-fit: cover;\n    max-height: 200px;\n    }\n  \n  .body-card .resto-card .name{\n    background-color:#D44000;\n    color: #ffffff;\n    padding:10px;\n    font-size:18px;\n  }\n\n  .resto-card .resto-item__content h3 {\n    margin: 0 0 10px 0;\n  }\n\n  .resto-card .resto-item__content h3 a {\n    color: #424242;\n    font-size: 20px;\n    padding: 12px 5px 12px 0;\n    text-decoration: none;\n  }\n\n  .resto-card .resto-item__content p {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    display: -webkit-box;\n    -webkit-line-clamp: 4; /* number of lines to show */\n    -webkit-box-orient: vertical;\n  }\n\n  .resto-card .resto-item__header .resto-item__header__rating {\n    position: absolute;\n    padding: 8px;\n    bottom: 20px;\n    left: 0;\n    display: inline-block;\n    background-color: #ffb69f;\n    color: white;\n    border-top-right-radius: 14px;\n  }\n\n  .resto-card .resto-item__header .resto-item__header__rating .resto-item__header__rating__score {\n    margin-left: 10px;\n    margin-right: 5px;\n  }\n  \n  .body-card .resto-card .resto-item__content {\n    padding: 16px;\n  }\n\n  .city {\n    position: absolute;\n    top: 8px;\n    left: 16px;\n    border-radius: 5px;\n    background-color: #ffb69f;\n    padding: 5px 10px 5px;\n    color: white;\n    font-weight: 500;\n  }\n  \n  /*\n  detail \n  */\n  #detail-section {\n    background-color: #F9F9F9;\n  }\n\n  .detail-header {\n    width: 100%;\n    display: grid;\n    grid-template-columns: 1fr;\n  }\n  .detail-hero{\n    width: 100%;\n    position: relative;\n    top: 30px;\n  }\n  .detail-hero img{\n    width: 100%;\n    border-bottom: 2px solid #808080;\n    border-radius: 10px 5px 10px 5px;\n  }\n  .detail-hero .detail-resto-rating{\n    position: absolute;\n    top:0;\n    right: 0;\n    background-color: #ffb69f;\n    color: #ffffff;\n    padding: 10px;\n    font-size: 15px;\n  }\n\n  .checked{\n    color: #111;\n  }\n\n  .detail-resto-title{\n    padding: 40px;\n  }\n  \n  .detail-resto-title .detail-resto-title-section{\n    margin-bottom: 22px;\n  }\n  .detail-resto-title .detail-resto-title-section h1{\n    font-size: 1.5em;\n    margin-bottom: 10px;\n    font-weight:bold;\n  }\n  .detail-resto-title .detail-resto-title-section h2{\n    font-size: 1.2em;\n  }\n  \n  .detail-resto-title .detail-resto-title-section p{\n    color: #3d3d3d;\n    font-style: italic;\n  }\n  \n  .detail-resto-title .detail-resto-info h2, .detail-resto-categories h2{\n    padding-bottom: 5px;\n    font-size: 1.2em;\n  }\n  .detail-resto-title .detail-resto-info p{\n    color: #3d3d3d;\n    text-align: justify;\n  }\n  .detail-resto-title .detail-resto-categories{\n    margin-top: 15px;\n  }\n  \n  .detail-resto-title .detail-resto-categories ul li{\n    display: inline-block;\n    padding: 5px;\n    margin:5px;\n    background-color: #ffb69f;\n    color: #ffffff;\n    border-radius: 5px 10px 5px 10px;\n  }\n  \n  .detail-resto-menu{\n    text-align: center;\n    width: 100%;\n    margin-top: 0;\n  }\n  .detail-resto-menu h1{\n    width:100%;\n    padding: 10px;\n    color: #424242;\n  }\n  .detail-resto-menu .menu{\n    width: 100%;\n    display: flex;\n    flex-direction: column;\n  }\n  .detail-resto-menu img{\n    display: block;\n    width: 100%;\n    height:240px;\n    border-bottom: 1px solid #f0b452;\n    border-radius: 10px 5px 10px 5px;\n  \n  }\n  \n  .menu h2{\n    /* text-decoration: underline; */\n    padding: 10px;\n    background-color: #ffb69f;\n    border-radius: 10px 10px 0 0;\n    color: #fff;\n  }\n  \n  .menu ul{\n    width: 100%;\n    padding: 20px;\n    background-color:#FDF4F5;\n    border: 1px solid #ffffff;\n    border-radius:  0 0 10px 10px;\n  }\n  \n  .menu ul li{\n    color:#111;\n    font-size: 16px;\n    margin-left: 20px;\n    padding: 5px;\n    list-style:circle;\n    text-align: justify;\n  \n  }\n  \n  .resto-review-section {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    margin-top: 40px;\n    padding: 25px;\n    height: 30%;\n    overflow: auto;\n    color: #111;\n    background-color: #FDF4F5;\n    margin-bottom: 0;\n    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,\n      rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;\n  }\n  \n  .resto-review-section h1{\n    margin-top: 50px;\n    margin-bottom: 50px;\n    color: #111;\n  }\n  .resto-review-section .resto-review-user {\n    width: 80%;\n    display: flex;\n    margin: 10px;\n    padding-bottom: 20px;\n    border-bottom: 1px solid #db0000;\n  }\n  \n  .resto-review-section .resto-review-user img {\n    width: 40px;\n    height: 40px;\n    margin: 7px;\n  }\n  \n  .resto-review-section .resto-review-user .resto-review-desc {\n    margin-top: 10px;\n    margin-bottom: 5px;\n    font-size: 17px;\n  }\n  \n  .resto-review-section .resto-review-user .resto-review-date {\n    font-size: 15px;\n    color: #111;\n  }\n\n  /* \n  favorite\n  */\n\n  .content {\n    width: 90%;\n    }\n\n  .content .content__heading {\n    font-weight: bold;\n    text-align: center;\n    margin-top: 30px;\n    margin-bottom: 30px;\n  }\n  .restos {\n    display: grid;\n    grid-template-columns: 1fr;\n    gap: 16px;\n    margin: 32px 0;\n  }\n\n  .restos .resto-card {\n    width: 100%;\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n    border-radius: 8px;\n    overflow: hidden;\n  }\n\n  .resto-card .resto-item__content {\n    padding: 16px;\n  }\n  \n  .resto-card .resto-item__content h3 {\n    margin: 0 0 10px 0;\n  }\n  \n  .resto-item__content h3 a {\n    color: #111;\n    text-decoration: none;\n  }\n  \n  .resto-card .resto-item__content p {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    display: -webkit-box;\n    -webkit-line-clamp: 4; /* number of lines to show */\n    -webkit-box-orient: vertical;\n  }\n\n  /* \n    footer\n  */\n  \n  footer {\n    margin-top: 40px;\n    background-color: #ffb69f;\n  }\n  \n  .title-footer {\n    text-align: center;\n    padding: 20px;\n    font-size: 14px;\n    color: #fff;\n  }\n\n  /* \n  Liked button\n  */\n  \n  .like {\n    font-size: 18px;\n    position: fixed;\n    bottom: 16px;\n    right: 16px;\n    background-color: #db0000;\n    color: white;\n    border: 0;\n    border-radius: 50%;\n    width: 55px;\n    height: 55px;\n    cursor: pointer;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n  }"],"sourceRoot":""}]);
